/*----- 3. Slider style  ------*/

.slider-height-1 {
  height: 750px;

  @media #{$xl-layout} {
    height: 650px;
  }

  @media #{$lg-layout} {
    height: 520px;
  }

  @media #{$md-layout} {
    height: 450px;
  }

  @media #{$xs-layout} {
    height: auto;
    padding: 50px 0 0;

    &.slider-height-res15 {
      padding: 0 0 0;
    }
  }

  @media #{$sm-layout} {
    padding: 38px 0 0;
  }

  &.slider-height-res {
    @media #{$xs-layout} {
      position: relative;

      height: 400px;

      &:before {
        position: absolute;
        top: 0;
        left: 0;

        width: 100%;
        height: 100%;

        content: "";

        opacity: 0.5;
        background-color: #fff;
      }
    }
  }
}

.single-slider {
  display: flex;
  align-items: flex-end;

  .slider-content {
    padding: 183px 0 0;

    @media #{$xl-layout} {
      padding: 80px 0 0;
    }

    @media #{$lg-layout} {
      padding: 45px 0 0;
    }

    @media #{$md-layout} {
      padding: 0 0 0;
    }

    @media #{$xs-layout} {
      padding: 0 0 40px;

      text-align: center;
    }

    @media #{$sm-layout} {
      padding: 0 0 0;

      text-align: left;
    }

    h3 {
      font-size: 24px;
      font-weight: 500;

      margin: 0;
    }

    h2 {
      color: #ffffff;
    }

    h1 {
      font-size: 72px;
      line-height: 97px;

      margin: 6px 0 37px;

      color: #010101;

      @media #{$lg-layout} {
        font-size: 52px;
        line-height: 65px;
      }

      @media #{$md-layout} {
        font-size: 40px;
        line-height: 52px;

        margin: 6px 0 20px;
      }

      @media #{$xs-layout} {
        font-size: 33px;
        line-height: 48px;

        margin: 6px 0 20px;
      }

      @media #{$sm-layout} {
        font-size: 30px;

        margin: 6px 0 10px;
      }
    }

    .slider-btn {
      a {
        font-size: 16px;
        line-height: 1;

        display: inline-block;

        padding: 19px 50px 21px;

        text-transform: uppercase;

        color: #010101;
        border: 1px solid #333;

        @media #{$md-layout} {
          padding: 15px 35px 17px;
        }

        @media #{$xs-layout} {
          padding: 15px 35px 17px;
        }

        &:hover {
          color: #fff;
          border: 1px solid $theme-color;
        }
      }
    }

    &.slider-content-10 {
      padding-top: 0;

      @media #{$xs-layout} {
        display: block;

        width: 100%;

        text-align: center;
      }

      @media #{$sm-layout} {
        text-align: left;
      }
    }
  }

  .slider-single-img {
    margin: 0 -80px 0 -80px;

    @media #{$xl-layout} {
      margin: 0 0 0 0;
    }

    @media #{$lg-layout} {
      margin: 0 0 0 0;
    }

    @media #{$md-layout} {
      margin: 0 0 0 0;
    }

    @media #{$xs-layout} {
      margin: 0 0 0 0;
    }
  }

  &.single-slider-10 {
    align-items: center;
  }
}

.swiper-slide .slider-content * {
  -webkit-animation-duration: 1.3s;
  animation-duration: 1.3s;

  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

.swiper-slide-active {
  .slider-animated-1 {
    h3 {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.0s;
      animation-delay: 1.0s;
    }

    h1 {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.3s;
      animation-delay: 1.3s;
    }

    h5 {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.5s;
      animation-delay: 1.5s;
    }

    p {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.5s;
      animation-delay: 1.5s;
    }

    a {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.6s;
      animation-delay: 1.6s;
    }

    &.slider-single-img img {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.6s;
      animation-delay: 1.6s;
    }

    &.single-slider-img4 img {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.6s;
      animation-delay: 1.6s;
    }

    &.slider-singleimg-hm9 img {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.6s;
      animation-delay: 1.6s;
    }

    &.slider-singleimg-hm11 img {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.6s;
      animation-delay: 1.6s;
    }

    .content-img img {
      -webkit-animation-name: fadeInUp;
      animation-name: fadeInUp;
      -webkit-animation-delay: 1.0s;
      animation-delay: 1.0s;
    }
  }

  .slider-animated-2 {
    h3 {
      -webkit-animation-name: fadeInLeft;
      animation-name: fadeInLeft;
      -webkit-animation-delay: 1.0s;
      animation-delay: 1.0s;
    }

    h1 {
      -webkit-animation-name: fadeInLeft;
      animation-name: fadeInLeft;
      -webkit-animation-delay: 1.3s;
      animation-delay: 1.3s;
    }

    a {
      -webkit-animation-name: fadeInLeft;
      animation-name: fadeInLeft;
      -webkit-animation-delay: 1.6s;
      animation-delay: 1.6s;
    }

    &.slider-singleimg-hm10 img {
      -webkit-animation-name: fadeInRight;
      animation-name: fadeInRight;
      -webkit-animation-delay: 1.6s;
      animation-delay: 1.6s;
    }
  }
}

.nav-style-1 {
  .ht-swiper-button-nav {
    font-size: 80px;

    position: absolute;
    top: 50%;
    left: 80px;

    display: inline-block;
    visibility: hidden;

    width: 80px;
    height: 80px;
    padding: 0;

    transition: all 0.3s ease 0s;
    transform: translateY(-50%);
    text-align: center;

    opacity: 0;
    color: #999;
    border: none;
    background: none;

    @media #{$xl-layout} {
      left: 30px;
    }

    @media #{$lg-layout} {
      left: 30px;
    }

    @media #{$md-layout} {
      font-size: 60px;

      left: 15px;
    }

    @media #{$xs-layout} {
      font-size: 50px;

      left: 15px;
    }

    &:hover {
      color: $theme-color;
    }

    &.swiper-button-next {
      right: 80px;
      left: auto;

      @media #{$xl-layout} {
        right: 30px;
      }

      @media #{$lg-layout} {
        right: 30px;
      }

      @media #{$md-layout} {
        right: 15px;
      }

      @media #{$xs-layout} {
        right: 15px;
      }
    }
  }

  &.nav-testi-style {
    .owl-nav {
      div {
        font-size: 50px;

        left: -155px;

        color: #8d8c8c;

        @media #{$xl-layout} {
          left: -80px;
        }

        @media #{$lg-layout} {
          left: -80px;
        }

        @media #{$md-layout} {
          left: -30px;
        }

        @media #{$xs-layout} {
          display: none;
        }

        &:hover {
          color: #3d6882;
        }

        &.owl-next {
          right: -155px;
          left: auto;

          @media #{$xl-layout} {
            right: -80px;
          }

          @media #{$lg-layout} {
            right: -80px;
          }

          @media #{$md-layout} {
            right: -30px;
          }
        }
      }
    }

    &.nav-testi-mrg {
      .owl-nav {
        div {
          left: -80px;

          color: #fff;

          @media #{$md-layout} {
            left: 0;
          }

          @media #{$xs-layout} {
            display: none;
          }

          &.owl-next {
            right: -80px;
            left: auto;

            @media #{$md-layout} {
              right: 0;
              left: auto;
            }

            @media #{$xs-layout} {
              display: none;
            }
          }
        }
      }
    }
  }
}

.slider-area {
  &:hover .nav-style-1 .ht-swiper-button-nav {
    visibility: visible;

    opacity: 1;
  }

  &.ml-10 {
    margin-left: 0;
  }

  @media #{$xl-layout} {
    &.ml-70 {
      margin-left: 15px;
    }

    &.mr-70 {
      margin-right: 15px;
    }
  }

  @media #{$lg-layout} {
    &.ml-70 {
      margin-left: 30px;
    }

    &.mr-70 {
      margin-right: 30px;
    }
  }

  @media #{$md-layout} {
    &.ml-70 {
      margin-left: 40px;
    }

    &.mr-70 {
      margin-right: 40px;
    }
  }

  @media #{$xs-layout} {
    &.ml-70 {
      margin-left: 15px;
    }

    &.mr-70 {
      margin-right: 15px;
    }
  }


  .swiper-pagination-bullets {
    position: absolute;
    right: 0;
    bottom: 26px;
    left: 0;

    text-align: center;

    .swiper-pagination-bullet {
      position: relative;

      display: inline-block;

      width: 14px;
      height: 14px;
      margin: 0 8px;

      opacity: 1;
      border-radius: 100%;
      background: #fff none repeat scroll 0 0;

      &:before {
        position: absolute;
        top: -3px;
        right: -3px;
        bottom: -3px;
        left: -3px;

        content: "";

        border: 1px solid transparent;
        border-radius: 100%;
      }

      &-active:before {
        border: 1px solid #fff;
      }
    }
  }
}

/* slider style-2 */
.slider-content-2 {
  margin-top: -6px;

  @media #{$xs-layout} {
    text-align: center;
  }

  h3 {
    font-size: 24px;
    font-weight: 500;

    position: relative;

    margin-bottom: 0;
    margin-left: 135px;

    /**color: #000000;**/
    color: #ffffff;

    @media #{$xs-layout} {
      margin-left: 0;
    }

    @media #{$sm-layout} {
      margin-left: 60px;
    }

    &::before {
      position: absolute;
      top: 14px;
      left: -135px;

      width: 120px;
      height: 2px;

      content: "";

      background-color: #000000;

      @media #{$xs-layout} {
        display: none;
      }

      @media #{$sm-layout} {
        left: 80px;

        display: block;

        width: 60px;
      }
    }

    &.no-style {
      margin-left: 0;

      @media #{$sm-layout} {
        margin-left: 0;
      }

      &:before {
        display: none;
      }
    }
  }

  h1 {
    font-size: 30px;
    line-height: 40px;

    margin: 6px 0 37px;

    /**color: #010101;**/
    color: #ffffff;

    @media #{$xl-layout} {
      font-size: 60px;
      line-height: 80px;
    }

    @media #{$lg-layout} {
      font-size: 56px;
      line-height: 70px;
    }

    @media #{$md-layout} {
      font-size: 45px;
      line-height: 60px;
    }

    @media #{$xs-layout} {
      font-size: 35px;
      line-height: 45px;
    }
  }

  .slider-btn {
    a {
      font-size: 16px;
      line-height: 1;

      display: inline-block;

      padding: 19px 50px 21px;

      text-transform: uppercase;

      color: #010101;
      border: 1px solid #333;
      background-color: #eeeff1;

      @media #{$xs-layout} {
        padding: 14px 30px 16px;
      }

      &:hover {
        color: #fff;
        border: 1px solid $theme-color;
      }
    }

    &--style2 {
      a {
        background-color: transparent;
      }
    }
  }

  &.slider-content-fruits {
    h3 {
      color: #6eab49;

      &::before {
        background-color: #6eab49;

        @media #{$sm-layout} {
          left: 45px;
        }
      }
    }

    h1 {
      font-size: 71px;

      @media #{$xl-layout} {
        font-size: 55px;
      }

      @media #{$lg-layout} {
        font-size: 50px;
      }

      @media #{$md-layout} {
        font-size: 40px;
      }

      @media #{$xs-layout} {
        font-size: 30px;

        margin: 6px 0 20px;
      }
    }

    .slider-btn {
      a {
        font-size: 16px;

        color: #fff;
        border: 1px solid #6eab49;
        background-color: #6eab49;

        &:hover {
          color: #fff;
          border: 1px solid $theme-color;
        }
      }
    }

    &--style2 {
      h3 {
        color: #ab87cd;

        &:before {
          background-color: #ab87cd;

          @media #{$sm-layout} {
            left: 45px;
          }
        }
      }

      .slider-btn {
        a {
          border: 1px solid #ab87cd;
          background-color: #ab87cd;
        }
      }
    }
  }

  &.slider-content-flower {
    h3 {
      letter-spacing: 0.8px;

      color: #c61a32;

      &::before {
        background-color: #c61a32;

        @media #{$sm-layout} {
          left: 45px;
        }
      }
    }

    h1 {
      font-size: 66px;

      @media #{$xl-layout} {
        font-size: 55px;
      }

      @media #{$lg-layout} {
        font-size: 50px;
      }

      @media #{$md-layout} {
        font-size: 40px;
      }

      @media #{$xs-layout} {
        font-size: 30px;

        margin: 6px 0 20px;
      }
    }

    .slider-btn {
      a {
        font-size: 16px;

        color: #fff;
        border: 1px solid #c61a32;
        background-color: #c61a32;

        &:hover {
          color: #fff;
          border: 1px solid $theme-color;
        }
      }
    }
  }

  &--style2 {
    h1 {
      margin-bottom: 5px;
    }

    p {
      font-size: 17px;
      font-weight: 400;

      margin-bottom: 50px;

      color: #73848e;
    }

    .slider-btn {
      a {
        background-color: transparent;
      }
    }

    &--white {
      h1 {
        color: $white;
      }

      h3 {
        color: $white;
      }

      .slider-btn {
        a {
          color: $white;
          border-color: $white;
        }
      }
    }
  }

  &--white {
    h1 {
      color: $white;
    }

    h3 {
      color: $white;
    }

    p {
      color: $white;
    }

    .slider-btn {
      a {
        color: $white;
        border-color: $white;
        background-color: transparent;
      }
    }
  }
}

.single-slider-2 {
  @media #{$xl-layout} {
