.slider-content-5 {
  padding-top: 66px;

  h3 {
    font-size: 30px;
    font-weight: 500;

    margin: 0;

    letter-spacing: 0.5px;

    color: #fff;
  }

  h1 {
    font-size: 72px;
    font-weight: 500;
    line-height: 56px;

    margin: 27px 0 22px;

    letter-spacing: 0.5px;

    color: #fff;

    @media #{$md-layout} {
      font-size: 58px;

      margin: 17px 0 20px;
